import React, {useState, useEffect} from 'react'; 
import data from './data.js';


const App = () => {

  let [list, setList] = useState(data);
  const [index, setIndex] = useState(0);


  useEffect(()=> {
    const lastIndex = list.length-1;

    if(index < 0){
      setIndex(lastIndex);
    }

    if(index > lastIndex){
      setIndex(0);
    }

  }, [list, index])


  return       ( <>
    {/* div soup */}

    {list.map((value, indexList)=> {
      const {image, name, id} = value;
      let position = 'nextSlide';

      if(indexList === index){
        position = 'activeSlide'
      }

      if(indexList === index-1 || (index === 0 && indexList === list.length-1)){
        position = 'lastSlide'
      }

      return (
        <article className={position} key={id}>
          <hr />
        <img src={image} alt=""/>
        <p>{name}</p>
      </article>
      )

    })}

    <div id="btnHolder">
      <button onClick={()=> setIndex(index-1)}>prev</button>
      <button onClick={()=> setIndex(index+1)}>next</button>
    </div>


      {/* div soup */}
      </>
)
};

export default App;